
.card {
  padding: 0px;
  background-color: white;
  border-radius: 0px;
  padding-bottom: 40px;
  height: 100%;
}
.card .img-card {
    background-position: center;
    background-size: cover;
    height: 120px;
    background-repeat: no-repeat;
}
.card .card-content {
    margin: 10px;
}
.card .content-event {
    height: 80px;
    overflow: auto;
}



