
footer {
  color: lightgrey;
  background-color: black;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
footer ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    list-style: none;
    padding-left: 0px;
    padding-right: 0px;
}
footer ul li {
      margin-right: 15px;
}
footer ul li:last-child {
        margin-right: 0px;
}
footer ul li a {
        color: lightgrey;
}
footer .m-t-md {
    margin-top: 30px;
}
footer hr {
    margin-top: 5px;
    margin-bottom: 10px;
    border-top: 1px solid lightgrey;
}
footer .social {
    -webkit-box-pack: end;
        -ms-flex-pack: end;
            justify-content: flex-end;
}
@media screen and (max-width: 991px) {
.social {
    -webkit-box-pack: center !important;
        -ms-flex-pack: center !important;
            justify-content: center !important;
}
.links {
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
}



