  background-color: black;
  background-position: center;
  background-repeat: no-repeat;
}
.event-content .hero .img-hero {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    background: rgba(0, 0, 0, 0.8);
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    height: 330px;
}
.event-content .hero .img-hero .container h3 {
      margin-top: -25px;
}
.event-content .raw-html img {
  max-width: 1140px;
  height: auto;
}



