
.subnav-gtask {
  height: 65px;
  margin-bottom: 0px;
}
.subnav-gtask a {
    font-size: 16px !important;
    padding-left: 0px !important;
    padding-right: 0px !important;
    margin-right: 30px;
    padding-top: 21px !important;
    padding-bottom: 20px !important;
}
.subnav-gtask .icon-subnav {
    font-size: 18px !important;
    margin-right: 8px;
}
.subnav-gtask .navbar-nav > .active > a {
    padding-bottom: 18px !important;
    color: #49A42E !important;
}
.subnav-gtask .navbar-nav > .active > a, .subnav-gtask .navbar-nav > .active > a:hover, .subnav-gtask .navbar-nav > .active > a:focus {
    background-color: #FFF;
    border-bottom: solid 1px #49A42E;
}
.subnav-gtask .navbar-brand {
    font-size: 20px !important;
}



