
.login-content {
  height: 100vh;
  background-color: white;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.login-content .login-box {
    padding-bottom: 75px;
}
.login-content .m-l-20 {
    margin-left: 20px;
}
.login-content .form-signin {
    max-width: 330px;
    padding: 15px;
    margin: 0 auto;
}
.login-content .form-signin .form-signin-heading, .login-content .form-signin .checkbox {
    margin-bottom: 10px;
}
.login-content .form-signin .checkbox {
    font-weight: normal;
}
.login-content .form-signin .form-control {
    position: relative;
    font-size: 16px;
    height: auto;
    padding: 10px;
    box-sizing: border-box;
}
.login-content .form-signin .form-control:focus {
    z-index: 2;
}
.login-content .form-signin input[type="text"] {
    margin-bottom: -1px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
}
.login-content .form-signin input[type="password"] {
    margin-bottom: 10px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
}
.login-content .account-wall {
    margin-top: 20px;
    padding: 40px 0px 20px 0px;
    background-color: #f7f7f7;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
}
.login-content .login-title {
    color: #555;
    font-size: 18px;
    font-weight: 400;
    display: block;
}
.login-content .profile-img {
    width: 96px;
    height: 96px;
    margin: 0 auto 10px;
    display: block;
    border-radius: 50%;
}
.login-content .need-help {
    margin-top: 10px;
}
.login-content .new-account {
    display: block;
    margin-top: 10px;
}



